body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.play-button {
	cursor: pointer;
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	right: 0;
	margin: auto;
	height: 60px;
	width: 100px;
	background-color: rgba(0, 0, 0, 0.7);
	border-radius: 5px;
}

.play-button:hover {
	background-color: rgba(0, 0, 0, 0.9);
}

.play-button:after {
	content: "";
	display: block;
	position: absolute;
	top: 16.5px;
	left: 40px;
	margin: 0 auto;
	border-style: solid;
	border-width: 12.5px 0 12.5px 20px;
	border-color: transparent transparent transparent rgba(255, 255, 255, 1);
}

.close-video::before {
	content: "\2716";
	cursor: pointer;
	position: absolute;
	right: 0;
	top: 0;
	font-size: 20px;
	padding: 20px;
	z-index: 1;
	line-height: 0.7;
	display: block;
	color: #fff;
}

.video-wrapper {
	position: relative;
	padding: 33.35% 0; /* 16:9 */
	height: 0;
}

.video-wrapper iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.app .image-gallery,
.app-sandbox {
	margin: 0 auto;
	width: 65%;
	-webkit-transition: all 1s ease;
	transition: all 1s ease;
}

@media (max-width: 1320px) {
	.app-sandbox-content {
		padding: 0 20px;
	}
}

.app-sandbox {
	margin: 40px auto;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	-o-user-select: none;
	user-select: none;
}

.app-buttons li {
	display: block;
}

@media (max-width: 768px) {
	.app-header {
		font-size: 20px;
	}

	.app-buttons li {
		display: block;
		margin: 10px 0;
	}

	.app-buttons li + li {
		padding: 0;
	}

	.play-button {
		height: 40px;
		width: 65px;
	}

	.play-button:after {
		top: 11px;
		left: 27px;
		border-width: 8.5px 0 8.5px 12px;
	}

	.close-video::before {
		font-size: 16px;
		padding: 15px;
	}
}

@media (max-width: 1024px) {
	.app .image-gallery,
	.app-sandbox {
		width: 100%;
	}
}

.app-interval-input-group {
	display: table;
}

.app-interval-label {
	display: table-cell;
	vertical-align: middle;
	padding: 6px 12px;
	font-size: 14px;
	font-weight: 400;
	line-height: 1;
	color: #555;
	text-align: center;
	background-color: #eee;
	border: 3px solid #ccc;
	border-right: none;
	border-radius: 4px;
	border-top-right-radius: 0;
	border-bottom-right-radius: 0;
}

.app-interval-input {
	-webkit-appearance: none;
	display: table-cell;
	margin: 0;
	padding: 9px;
	border-radius: 5px;
	font-size: 14px;
	border: 3px solid #ccc;
	background: #fff;
	width: 100%;
	border-top-left-radius: 0;
	border-bottom-left-radius: 0;
}

input.app-interval-input {
	width: 65px;
}

.app-checkboxes {
	margin-top: 10px;
}

.app-checkboxes li {
	display: block;
}

.image-gallery-right-nav {
  right: -125px;
}
.image-gallery-left-nav {
	left: -125px;
}

.container > div > div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.text {
  pointer-events: none;
  justify-content: start !important;
  font-family: 'Kanit', sans-serif;
  line-height: 0px;
  text-transform: uppercase;
}

.number {
  font-size: 450px;
  color: #373c4c;
}

.number span {
  display: inline-block;
  position: relative;
  -webkit-transform: translate3d(-35%, 0, 0);
          transform: translate3d(-35%, 0, 0);
}

.header {
  margin-left: 350px;
  font-size: 70px;
  color: white;
}

.stripe {
  height: 2px;
  width: auto;
}

.slopeBegin {
  background-color: #20232f;
  -webkit-clip-path: polygon(20vw 0, 70% 0, calc(70% - 20vw) 100%, 0 100%);
          clip-path: polygon(20vw 0, 70% 0, calc(70% - 20vw) 100%, 0 100%);
}

.slopeEnd {
  -webkit-clip-path: polygon(70% 0, 100% 0, calc(100% - 20vw) 100%, calc(70% - 20vw) 100%);
          clip-path: polygon(70% 0, 100% 0, calc(100% - 20vw) 100%, calc(70% - 20vw) 100%);
}

.slopeBegin,
.slopeEnd {
  position: absolute;
  width: 140%;
  height: 100%;
  cursor: pointer;
}


#root {
  background-color: #18445F;
  -webkit-transition: background-color 2s; /* For Safari 3.1 to 6.0 */
  transition: background-color 2s;
}


.pink {
  background: -webkit-gradient(linear, left top, right top, from(deeppink), to(coral));
  background: -webkit-linear-gradient(left, deeppink 0%, coral 100%);
  background: linear-gradient(to right, deeppink 0%, coral 100%);
}

.teal {
  background: -webkit-gradient(linear, left top, right top, from(SlateBlue), to(DeepSkyBlue));
  background: -webkit-linear-gradient(left, SlateBlue 0%, DeepSkyBlue 100%);
  background: linear-gradient(to right, SlateBlue 0%, DeepSkyBlue 100%);
}

.tomato {
  background: -webkit-gradient(linear, left top, right top, from(tomato), to(gold));
  background: -webkit-linear-gradient(left, tomato 0%, gold 100%);
  background: linear-gradient(to right, tomato 0%, gold 100%);
}


